<template>
    <div class="filters" >
        <v-filter v-for="(filter, index) in filters" :filter="filter"></v-filter>
    </div>
</template>

<script>
    import Filter from './Filter.vue'
    export default
    {
        props:{
            filters:{
                default: [],
                type: Array
            }
        },
        components:{
            'v-filter': Filter
        }
    }
</script>

<style>
</style>



