<template>
    <div class="v-searcher">
        <v-filters :filters="filters"></v-filters>
        <v-results></v-results>
    </div>
</template>

<script>
    import Filters from './components/Filters.vue'
    import Results from './components/Results.vue'
    export default
    {
        props: {
            id: {},
            locale: {},
            rows: {},
            controller: {},
            query: {},
            filters: {
                type: Array
            },
            showForm: {}
        },
        components: {
            'v-filters': Filters,
            'v-results': Results
        }
    }
</script>

<style>
</style>



